body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,
body {
    height: 100%;
}

.App {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.App .container {
    flex: 1 0 auto;
}

img {
    width: 100%;
}

.landing {
    position: relative;
    background: url(/static/media/showcase.786d8c16.jpg) no-repeat;
    background-size: cover;
    background-position: center;
    height: 100vh;
    margin-top: -24px;
    margin-bottom: -50px;
}

.landing-inner {
    padding-top: 80px;
}

.dark-overlay {
    background-color: rgba(0, 0, 0, 0.7);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.card-form {
    opacity: 0.9;
}

.latest-profiles-img {
    width: 40px;
    height: 40px;
}

.form-control::-webkit-input-placeholder {
    color: #bbb !important;
}

.form-control::-ms-input-placeholder {
    color: #bbb !important;
}

.form-control::placeholder {
    color: #bbb !important;
}
