html,
body {
    height: 100%;
}

.App {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.App .container {
    flex: 1 0 auto;
}

img {
    width: 100%;
}

.landing {
    position: relative;
    background: url('./img/showcase.jpg') no-repeat;
    background-size: cover;
    background-position: center;
    height: 100vh;
    margin-top: -24px;
    margin-bottom: -50px;
}

.landing-inner {
    padding-top: 80px;
}

.dark-overlay {
    background-color: rgba(0, 0, 0, 0.7);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.card-form {
    opacity: 0.9;
}

.latest-profiles-img {
    width: 40px;
    height: 40px;
}

.form-control::placeholder {
    color: #bbb !important;
}